import React, { Component } from 'react';
//Components
//Includes
import instagramIcon from '../../Assets/images/social-media-icons/ig-icon.png'
import facebookIcon from '../../Assets/images/social-media-icons/fb-icon.png'
import twitterIcon from '../../Assets/images/social-media-icons/twitter-icon.png'

class Footer extends Component {
    render() {
        return (
            <footer className="container-fluid">
                {/* 
                <div className="row">
                    <div className="col-md-4">
                        Our Location
                        </div>
                    <div className="col-md-4">
                        Navigation
                        </div>
                    <div className="col-md-4">
                        Follow Us
                        </div>
                </div>
                */}

                <div className="row mt-1">
                    <div className="col-md-6">
                        &copy; All Rights Reserved By Business Name
                    </div>
                    <div className="col-md-6 social-media-links">

                        <a href="/#" className="ml-2">
                            <img src={instagramIcon} alt="instagram-logo-icon" width="20" />
                        </a>
                        <a href="#/"  className="ml-2">
                            <img src={facebookIcon} alt="facebook-logo-icon" width="20" />
                        </a>
                        <a href="/#" className="ml-2">
                            <img src={twitterIcon} alt="twitter-logo-icon" width="20" />
                        </a>
                    </div>
                </div>

            </footer>
        )
    }
}
export default Footer;